import { AttackerData, DefenderData } from '../models/unit-data.model';
import { unitStats as UNIT_STATS } from '../data/unit-stats.const';
import { calculateMaxHp } from './calculate-max-hp.util';
import { freezingUnits, noDefenseUnits } from '../data/unit-list.const';

const BOOSTED_FACTOR = .5;
const POISONED_FACTOR = .8;

export function calculateDamage(attacker: AttackerData, defender: DefenderData) {
  const attackerStat = UNIT_STATS[attacker.unit];
  const defenderStat = UNIT_STATS[defender.unit];

  const attackerMaxHp = calculateMaxHp(attacker);
  const defenderMaxHp = calculateMaxHp(defender);

  let attackStat = attackerStat.attack;
  if (attacker.isBoosted) {
    attackStat += BOOSTED_FACTOR;
  }

  let defenseStat = defenderStat.defense;
  let defenseBonus = 1;
  if (defender.isPoisoned) {
    defenseBonus = POISONED_FACTOR;
  } else if (defender.withDefenseBonus && !defender.isPoisoned) {
    defenseBonus = defender.withDefenseBonus === 'withWall' ? defenderStat.wallBonus : defenderStat.defenseBonus;
  }

  const attackForce = attackStat * ((attacker.hp / attackerMaxHp) as any).toFixed(16);
  const defenseForce = defenseStat * defenseBonus * ((defender.hp / defenderMaxHp) as any).toFixed(16);
  
  const totalForce = attackForce + defenseForce;
  const accelerator = 4.5;

  const attackDamageTemp = ((attackForce / totalForce) as any).toFixed(16) * attackStat * accelerator as any;
  const defenseDamageTemp = ((defenseForce / totalForce) as any).toFixed(16) * defenseStat * accelerator as any;
  const attackDamage = Math.round(attackDamageTemp);
  let defenseDamage = Math.round(defenseDamageTemp);

  const defenderNewHp = Math.max(defender.hp - attackDamage, 0);
  let splashedNewHp = null;
  if (attacker.unit === 'Fire Dragon') {
    splashedNewHp = Math.max(defender.hp - Math.floor(attackDamage / 2), 0);
  }
  let attackerNewHp = attacker.hp;
  const noRetaliation = attacker.freeFromRetaliation 
                          || noDefenseUnits.includes(defender.unit)
                          || freezingUnits.includes(attacker.unit);
  if (defenderNewHp > 0 && !noRetaliation) {
    attackerNewHp = Math.max(attacker.hp - defenseDamage, 0);
  } else {
    defenseDamage = 0;
  }

  return { attackDamage, defenseDamage, defenderNewHp, attackerNewHp, splashedNewHp };
}
